.ProfileContainer {
  max-width: 900px;
  margin: auto;
  padding-top: 20px;
}

.HeroSection {
  position: relative;
  height: 300px;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.ProfileContent {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: white;
  gap: 10px;
}

.ProfileImage {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border: 5px solid white;
  margin-bottom: 10px;
}

.ProfileText {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Status {
  font-family: "Kranky", serif;
  font-weight: 800;
  font-size: 150%;
  color: #00bae2;
}

.ThemeSong {
  font-size: 1.2rem;
  margin-top: 5px;
}

.ProfileNav {
  margin-top: 20px;
}

.TabContent {
  margin-top: 20px;
  color: #00bae2;
  padding-bottom: 10px;
}

.NavLink {
  color: #00bae2;
}

.NavLink:hover {
  color: #ff0000;
}

.Active {
  color: yellow;
}

.EditIcon {
  text-decoration: none;
  color: #00bae2;
}

.EditIcon:hover {
   color: #ff0000;
}