@keyframes fly {
    0% {
      left: -10%;
      transform: rotate(10deg);
    }
    50% {
      transform: rotate(0deg);
    }
    100% {
      left: 110%;
      transform: rotate(-5deg);
    }
  }
  
  @keyframes fadeInAndMove {
    0% {
      left: -10%;
      opacity: 0;
    }
    50% {
      left: 50%;
      opacity: 0.5;
    }
    100% {
      left: 110%;
      opacity: 1;
    }
  }
  
  .airplane {
    position: absolute;
    font-size: 2.5rem; 
    color: #00bae2; 
    top: 20%;
    left: -10%;
    animation: fly 2s linear forwards;
  }
  
  